.container{
    margin: 0 auto;
    padding-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 50px;
}

.infoBorder{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
    height: auto;
    background: grey;
    border-radius: 2rem;
    gap: 20px;
    padding: 30px;
}

.title{
    background: black;
    border-radius: 2rem;
    padding: 10px 50px;
}

.title h1{
    font-size: 30px;
}

.text{
    background: black;
    border-radius: 2rem;
    padding: 10px 15px;
}

.text p{
    font-size: 20px;
}

