
.home{
    margin: 150px 0;
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}

.homeLeft{
    display: flex;
    flex-direction: column;
    width: 40%;
    align-items: center;
    justify-content: center;
}
.homeRight{
    z-index: 1;
    display: flex;
    flex-direction: column;
    gap: 100px;
    width: 60%;
    align-items: center;
    justify-content: center;
}

.homeRight img{
    height: 400px;
    width: 400px;
}

.text2{
    background: black;
    border-radius: 2rem;
    padding: 10px 50px;
}

.text2 h2{
    font-size: 60px;
    color: white;
    font-family: ElfBoyClassic;
}

.border{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 60px;
    background: black;
    border-radius: 2rem;
    padding: 30px 30px;
}
.title{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.text{
    width: 340px;
    word-break: break-all;
}

.btn {
    display: flex;
    flex-direction: row;
    gap: 30px;
}

.video{
    position: absolute;
    z-index: -1;
}

.video video{
    width: 100%;
    height: 100%;
}

