.container{
    height: 100%;
    margin: 0 auto;
    padding: 30px 0 50px 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 80%;
    background: grey;
    border-radius: 2rem;
    gap: 40px;
}

.titleInfo{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background: black;
    border-radius: 2rem;
}

.titleInfo h1{
    font-size: 40px;
}

.border{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background: black;
    padding: 10px;
    border-radius: 2rem;
    gap: 2rem;
    width: 610px;
    height: 321px;
}

.image{
    width: 150px;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.image img{
    width: 100%;
    height: 100%;
    border-radius: 10rem;
    border: 2px solid yellow;
}

.info{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /*gap: 2rem;*/
    height: 100%;

}

.title{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;
    height: 100%;
}

.title h1{
    font-size: 30px;
}

.title h3{
    font-size: 20px;
}

.text{
    width: 404px;
    padding: 20px;
}