@import url('https://fonts.googleapis.com/css2?family=Comfortaa&display=swap');

@font-face {
  font-family: ElfBoyClassic;
  src: url("./Assets/Fonts/Elfboyclassic.ttf");
}

*{
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

html{
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: black;
}

::-webkit-scrollbar-thumb {
  background: greenyellow;
  border-radius: 10px;
}

audio::-webkit-media-controls-panel {
  background: greenyellow;
}

audio::-webkit-media-controls-play-button {
  background-color: #B1D4E0;
  border-radius: 50%;
}

audio::-webkit-media-controls-mute-button{
  background-color: #B1D4E0;
  border-radius: 50%;
}

audio::-webkit-media-controls-mute-button:hover{
  background-color: rgba(177,212,224, .7);
}

audio::-webkit-media-controls-play-button:hover {
  background-color: rgba(177,212,224, .7);
}

audio::-webkit-media-controls-current-time-display{
  display: none;
}

audio::-webkit-media-controls-time-remaining-display{
  display: none;
}

body{
  font-family: 'Comfortaa', cursive;
  font-weight: normal;
  font-size: 25px;
  background: black;
  color: white;
  line-height: 1.7;
}

h1, h2, h3 ,h4, h5, p{
  font-family: 'Comfortaa', cursive;
  line-height: 2.9rem;
  color: white;
}

h1{
  color: yellow;
  font-size: 60px;
}

h2{
  font-family: ElfBoyClassic;
}

.container{
  height: 100%;
  margin: 0 auto;
}


button{
  font-size: 30px;
  width: 130px;
  height: 50px;
  border: none;
  outline: none;
  color: #fff;
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
}

button:before {
  content: '';
  background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity .3s ease-in-out;
  border-radius: 10px;
}

button:active {
  color: #000
}

button:active:after {
  background: transparent;
}

button:hover:before {
  opacity: 1;
}

button:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: #111;
  left: 0;
  top: 0;
  border-radius: 10px;
}

button a {
  color: white;
}

@keyframes glowing{
  0% { background-position: 0 0; }
  50% { background-position: 400% 0; }
  100% { background-position: 0 0; }
}

.element style{
  display: none;
}