.navbar{
    background: transparent;
    height: 80px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    position: sticky;
    top: 0;
    z-index: 999;
    transition: 0.2s;
}

.elements{
    display: flex;
    flex-direction: row;
    gap: 20px;
    align-items: center;
}

.navbar.active{
    background-color: black;
}

.items {
    display: flex;
    gap: 1.2rem;
    width: 7rem;
    justify-content: center;
}

.items a.active{
    border-radius: 2rem;
    padding: 6px;
    color: black;
    background: yellow;
    transition: 0.3s;
}

.item:hover{
    color: yellow;
}

.item{
    color: white;
    transition: 0.5s;
}

.titles{
    padding-left: 20px;
}

.titles h1{
    font-family: ElfBoyClassic;
    font-size: 60px;
}

.audio1{
    display: flex;
    justify-content: center;
    align-items: center;
}

