.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    gap: 200px;
}

.container1{
    display: flex;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    width: 75%;
    height: 100%;
}

.container2{
    display: flex;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    width: 75%;
    height: 100%;
}

.addReview{
    background: greenyellow;
    border-radius: 2rem;
}

.addReview form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    padding: 20px;
}

.addReview h1{
    background: black;
    padding: 15px;
    border-radius: 8px;
    color: white;
    font-size: 45px;
}

.addReview textarea{
    width: 100%;
    background: black;
    border-radius: 8px;
    padding: 1.5rem;
    font-size: 15px;
    width: 75%;
    color: white;
}

.addReview button{
    font-size: 20px;
}

.border{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: greenyellow;
    border-radius: 2rem;
    padding: 20px;
    gap: 20px;
    width: 50%;
}

.border h3{
    background: black;
    border-radius: 8px;
    padding: 10px;
}

.border p{
    background: black;
    border-radius: 8px;
    padding: 10px;
}


